<template>
  <div class="mobile-container">
    <transition name="mobile-overlay">
      <div v-if="showMobileMenu" class="mobile-overlay"></div>
    </transition>
    <transition name="mobile-menu">
      <div v-if="showMobileMenu" class="mobile-menu">
        <div class="mobile-menu__wrapper w-100 h-100">
          <div class="mobile-menu__header">
            <div class="mobile-menu__control-top flex">
              <div class="mobile-menu__location">
                <select-locations @click.native="locationModalOpen"/>
              </div>
              <button class="mobile-menu__clos" @click="mobileMenuClos">
                <v-icon>icon-clos</v-icon>
              </button>
            </div>
            <catalog-drop-down />
          </div>
          <div class="mobile-menu__body">
            <router-link v-for="(item, index) in buttonControl" :key="index" class="mobile-menu__button-control w-100" tag="button" :to="{ name: item.link }">
              <v-icon>{{ item.icon }}</v-icon>
              <span>{{ item.name }}</span>
            </router-link>
          </div>
          <div class="mobile-menu__footer">
            <footer-drop-down :conditions="conditions" :about="about" :buyers="buyers" />
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>

import FooterDropDown from '@/components/footer/FooterDropDown'
import CatalogDropDown from '@/components/mobile-menu/CatalogDropDown'
import Footer from '@/components/footer/mixins/Footer'
import SelectLocations from '@/components/header/SelectLocations'

export default {
  name: 'mobile-menu',
  mixins: [Footer],
  data: () => ({
    buttonControl: [
      {
        name: 'Войти/Регистрация',
        icon: 'icon-account',
        link: 'error'
      },
      {
        name: 'Магазины',
        icon: 'icon-location',
        link: 'error'
      },
      {
        name: 'Доставка и оплата',
        icon: 'icon-delivery',
        link: 'error'
      }
    ]
  }),
  computed: {
    showMobileMenu () {
      return this.$store.state.ui.mobileMenu.open
    }
  },
  components: {
    FooterDropDown,
    CatalogDropDown,
    SelectLocations
  },
  methods: {
    mobileMenuClos () {
      this.$store.dispatch('closMobileMenu')
    },
    locationModalOpen () {
      this.mobileMenuClos()
      this.$store.dispatch('openLocationModal')
    }
  },
  watch: {
    showMobileMenu (val) {
      if (val) {
        document.documentElement.style.overflow = 'hidden'
        return
      }
      document.documentElement.style.overflow = 'auto'
    }
  }
}
</script>

