<template>
  <transition name="search">
    <div v-if="showSearch" class="search-modal">
      <div class="search-modal__close w-100">
        <button class="search-modal__close__button" @click="close">
          <v-icon>icon-clos</v-icon>
        </button>
      </div>
      <div class="search-modal__container w-100">
        <input class="search-modal__input w-100" @input="filterCategories" v-model="productInput" type="text" :placeholder="placeholder">
          <div class="search-modal__lists-wrapper">
            <ul class="search-modal__popular-requests" v-if="!productInput">
              <div class="search-modal__popular-title">
                Популярные запросы
              </div>
              <li class="search-modal__popular-item" v-for="product in popularSearchedCategories" :key="product.title">
                <a :href="product.url">
                  {{ product.title }}
                </a>
              </li>
            </ul>
            <ul class="search-modal__popular-requests" v-else>
              <li class="search-modal__popular-item item-input" v-for="category in categoriesSearchList" :key="category.title">
                <a>
                  {{ category }}
                </a>
              </li>
            </ul>

            <!-- <div class="search-modal__popular-products">
              <div class="search-modal__popular-title">
                Популярные товары
              </div>
              <div class="search-modal__products-wrapper">
                <div class="popular-product" v-for="product in popularProducts" :key="product.title">
                  <a :href="product.url">
                    <img :src="product.image" alt="img">
                  </a>
                  <p>{{product.title}}</p>
                  <span v-html="product.price"></span>
                </div>
              </div>
            </div> -->
            <div class="search-modal__popular-products" v-if="productInput">
              <div class="search-modal__popular-link">
                <a href="#">
                  Показать все
                </a>
              </div>
              <div class="search-modal__products-wrapper">
                <div class="popular-product" v-for="product in productsSearchList" :key="product.title">
                  <a :href="product.url">
                    <img :src="product.image" alt="img">
                  </a>
                  <p>{{product.title}}</p>
                  <span v-html="product.price"></span>
                </div>
              </div>
            </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'search-modal',
  data: () => ({
    placeholder: 'Что ищем?',
    productInput: '',
    productList: require('@/assets/Products.json'),
    popularSearchedCategories: [
      {
        title: 'Converse',
        url: 'https://amersport.ru/brand/converse'
      },
      {
        title: 'Dr.Martens',
        url: 'https://amersport.ru/brand/drmartens'
      },
      {
        title: 'Havaianas',
        url: 'https://amersport.ru/brand/havaianas'
      },
      {
        title: 'Saucony',
        url: 'https://amersport.ru/brand/saucony'
      },
      {
        title: 'UGG',
        url: 'https://amersport.ru/brand/ugg'
      }
    ]
  }),
  methods: {
    filterCategories () {
      const a = []
      this.productList.map(item => {
        if (this.productInput.length && item.title.toLowerCase().substr(0, this.productInput.length).includes(this.productInput.toLowerCase())) {
          // const pr = item.title.replace(item.title[this.productInput.length], `<strong>${this.productInput}</strong>`)
          a.push(item.title)
          this.$store.dispatch('updateCategories', a)
        }
      })
    },
    close () {
      this.$store.dispatch('closeSearchModal')
    }
  },
  computed: {
    ...mapGetters(['getFilteredCategories']),
    popularProducts () {
      return this.productList.filter(product => product.type === 'product')
    },
    categoriesSearchList () {
      return this.getFilteredCategories.filter(item => {
        if (this.productInput.length && item.toLowerCase().substr(0, this.productInput.length).includes(this.productInput.toLowerCase())) {
          return item
        }
      })
    },
    productsSearchList () {
      return this.productList.filter(item => {
        if (item.type === 'product' && item.title.toLowerCase().includes(this.productInput.toLowerCase())) {
          return item.title
        }
      })
    },
    showSearch () {
      return this.$store.state.ui.searchModal.open
    }
  },
  watch: {
    showSearch (val) {
      if (val) {
        document.documentElement.style.overflow = 'hidden'
        return
      }
      document.documentElement.style.overflow = 'auto'
    }
  }
}
</script>

